import React from 'react';
//import {useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Typography} from '@material-ui/core';
//import Img from 'gatsby-image';
import {Link, //navigate, 
        useStaticQuery, graphql} from 'gatsby';

//import Context from 'gatsby-theme-blog-starter/src/components/Context';

const useStyles = makeStyles(theme => ({
  logo: {
    flexGrow: 1,
    marginLeft: theme.spacing(2),
  },
  logoClick: {
    display: 'inline-block',
    '&:hover': {
      cursor: 'pointer'
    }
  },
  footer: {
    padding: theme.spacing(1),
    marginTop: 'auto',
    backgroundColor: theme.palette.primary.dark,
    width: '100%',
    display: 'flex',
    flexDirection: 'column'
  },
  '@media (min-width: 1024px)': {
    footer: {
      flexDirection: 'row',
      alignItems: 'center'
    }
  },
  text: {
    color: theme.palette.primary.contrastText,
    marginLeft: theme.spacing(2)
  },
  textLink: {
    color: theme.palette.primary.contrastText,
    '&:hover': {
      color: 'black',//theme.palette.secondary.main
    }
  },
  menuLink: {
    color: theme.palette.primary.contrastText,
    margin: theme.spacing(2),
    '&:hover': {
      color: 'black'//theme.palette.secondary.main
    }
  },
  activeLink: {
    background: theme.palette.primary.light,
    borderRadius: theme.spacing(1),
    padding: theme.spacing(1),
    '&:hover': {
      color: 'white'
    }
  }
}));

const Footer = () => {
  const classes = useStyles();
  //const contextData = useContext(Context);

  const { site: {siteMetadata : {//options: {basePath}, 
                                  footerLinks, displayFooterMessage, footerMessage, author, email}}} = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            options {
              basePath
            }
            footerLinks {
              name
              link
            }
            displayFooterMessage
            footerMessage
            author
            email
          }
        }
      }
    `
  )

  return (
    <footer className={classes.footer}>


      {
        displayFooterMessage ? 
        <Typography variant="body1" className={classes.text} align='center'>
          {footerMessage}
          <a className={classes.textLink} href={'mailto:'+email}>{author}</a>
        </Typography>
        : null
      }
      {
        footerLinks.filter(d => d.name).map(link => (
          <Typography key={link.name}>
            <Link key={link.name} className={classes.menuLink} activeClassName={classes.activeLink} to={link.link}>{link.name}</Link>
          </Typography>
      ))
      }
    </footer>
  );
}


export default Footer;

